@import url(https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  font-family: 'Kanit', sans-serif;
  box-sizing: border-box;
}
body {
  line-height: inherit;
  background-color: #09090b;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  background-color: #09090b;
  height: 100vh;
}

.justify-column {
  display: flex;
  flex-direction: column;
  grid-gap: 0px;
  gap: 0px
}

.header-content {
  width: 90vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.content {
  width: 90vw;
  text-align: start;
}

.title {
  font-size: 2rem;
  font-weight:600;
  color: white;
  line-height: 1;
  margin: 0;
}

.h2 {
  color: white;
  font-size: 1.7rem;
  line-height: 1;
}

.h3 {
  color: white;
  font-size: 2rem;
  font-weight: 300;
  line-height: 1;
}

.desc {
  color: #BABABA;
  font-size: 1.5rem;
  font-weight: 200;
}

a {
  text-decoration: none;
}

.btn1 {
  font-size: 1rem;
  padding: .5rem;
  padding-left: 2rem;
  padding-right: 2rem;
  border-radius: 9999px;
  border: none;
  cursor: pointer;
  justify-content: center;
  display: flex;
  align-items: center;
  grid-gap: .25rem;
  gap: .25rem;
  max-width: 32rem;
  transition: transform 0.3s ease-out; /* Transição suave da transformação */
}

.btn1 svg {
  padding-top: .2rem;
}

.btn1:hover {
  transform: scale(1.05);
}

.pages {
  display: flex;
  flex-direction: column;
}

.line {
  background-color: rgb(39, 39, 42);
  height: 1px;
  width: 100vw
}

.line-sidebar {
  background-color: rgb(39, 39, 42);
  height: 1px;
  width: 85%;
}
.line-sidebarr {
  background-color: rgb(39, 39, 42);
  height: 1px;
  width: 100%;
}

.init {
  display: flex;
  flex-direction: column;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}


.padding {
  padding-top: 1rem;
  padding-bottom: 1.5rem;
}

@media screen and (min-width: 1500px) {
  .header-content {
    width: 75vw;
  }
}

@media screen and (min-width: 1425px) {
  .header-content {
    width: 55vw;
  }
  .title {
    font-size: 3.5rem;
  }
  .h2 {
    font-size: 2rem;
  }
  .desc {
    font-size: 1.5rem;
  }
  .content {
    width: 55vw;
  }

  /* Outras regras CSS específicas para 1024px podem ser adicionadas aqui */
}
.sidebar {
    position: fixed;
    left: -350px; /* Inicialmente fora da tela */
    top: 0;
    bottom: 0;
    width: 250px;
    padding: 1.5rem;
    background-color: #111111;
    transition: left 0.3s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition-duration: .5s;
    border: 0 solid #333333;
    border-right-width: 1px;
    z-index: 100;
  }
  
  .sidebar.open {
    left: 0; /* Mostra a navegação quando aberta */
  }
  
  .toggle-btn {
    display: block;
    width: 100%;
    padding: 15px;
    background-color: #555;
    color: #fff;
    border: none;
    cursor: pointer;
  }
  
  .sidebar-nav {
    display: flex;
    width: 100%;
    justify-content: start;
    text-align: start;
    padding: 20px;
  }
  
  .sidebar-nav ul {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    padding: 0;
    margin: 0;
  }
  
  .sidebar-nav ul li {
    margin-bottom: 10px;
  }

  .sidebar-nav ul li a {
    color: #fff;
    text-decoration: none;
    font-size: 1.2rem;
  }
  
  .sidebar-nav ul li a:hover {
    color: #ffca28;
  }

  .headerBTN {
    font-size: 1rem;
    font-weight: 300;
    color: white;
    background-color: transparent;
    border: none;
    cursor: pointer;
    padding: 10px;
    transition: background-color 0.3s;
    border-radius: 0.5rem;
    }
    
    .headerBTN:hover {
        background-color: #242424;

    }

.flex {
    display: flex;
    grid-gap: .75rem;
    gap: .75rem
}

.rounded {
    display: inline-block;
    position: relative;
    overflow: hidden; /* Para garantir que a imagem não ultrapasse o contêiner */
    border: 0 solid rgb(39, 39, 42);
    border-width: 0.1rem;
    border-radius: 1rem;
    transition: transform 0.3s;
}

.rounded:hover {
    transform: scale(1.05);
}

.pageImg {
    display: block;
    width: 100%;
    height: auto;
    object-fit: cover;
    opacity: .3;
    aspect-ratio: 16/9;
    border-radius: 1rem;
    transition: opacity 0.3s;
}

.pageImg:hover {
    opacity: .5;
}

.absolute {
    position: absolute;
    margin: .6rem;
    line-height: 0;
}

.bottom {
    bottom: 0;
}

.top {
    top: 0;
}

.btn {
    border: none;
    border-radius: .2rem;
    display: flex;
    padding: .3rem;
}

p {
    font-size: 1rem;
    font-weight: 200;
    color: white;
}
@media screen and (max-width: 1025px) {
    .flex {
        flex-direction: row;
        flex-wrap: wrap
    };
    .rounded {
        width: 47.5%;
    }
}

.footer {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.round {
    background-color: #121212;
    border: 0 solid rgb(68, 68, 68);
    border-width: 1px;
    border-radius: 1rem;
}


.itens {
    padding: 1rem;
    justify-content: center;
    align-items: center;
    display: flex;
}

.itens-grid {
    grid-gap: .75rem;
    gap: .75rem;
    display: flex;
}

.shrinks {
    width: 100%;
    height: 1px;
    background-color: rgb(68, 68, 68);
}

.span {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 1.25rem;
}

.span span {
    color: white;
    font-weight: 100;
}

.footerBtn {
    background-color: #09090b;
    border: 0 solid #123123;
    border-color: rgb(68, 68, 68);
    border-width: 1px;
    height: 2.25rem;
    width: 2.25rem;
    display: flex;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    border-radius: .3rem;
}

.footerBtn:hover {
    background-color: rgb(68, 68, 68);
}

.a {
    width: 20px;
    height: 20px;
}
.imgME {
    max-width: 100%;
    height: auto;
    border-radius: .7rem
}

.about {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
}

.about-text {
    max-width: 32rem;
    text-align: start;
}

.about-text p {
    font-size: 1.2rem;
    line-height: 1.625rem;
    color: #A1A1A1
}
.hl {
    font-weight: 300;
    color: white;
}


@media screen and (min-width: 1025px) {
    .about {
        flex-direction: row;
    }
}
.scale {
    transition: .5s
}

.scale:hover {
    transform: scale(1.05);
}

.padding-more {
    padding-top: 3rem
}

.project {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    line-height: 1;
}

.project-desc {
    display: flex;
    flex-direction: column;
    justify-content: start;
    grid-gap: 2rem;
    gap: 2rem;
}

.project-desc p {
    font-size: 1.4rem;
    color: #A1A1A1;
}

.project-buttons {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem
}

.stat {
    position: relative;
    font-size: .875rem;
    line-height: 1.25rem;
    align-items: center;
    display: flex;
}

.status {
    padding-left: 0rem;
    width: .5rem;
    height: .5rem;
    display: flex;
    position: absolute;
}

.status-ball {
    position: absolute;
    display: inline-flex;
    height: 100%;
    width: 100%;
    animation: ping 1s cubic-bezier(0,0,.2,1) infinite;
    border-radius: 9999px;
    background-color: rgb(82, 239, 68);
    padding-left: .25rem;
    opacity: .75;
}

.status-shadow {
    padding-left: .5rem;
    --tw-bg-opacity: 1;
    background-color: rgb(68, 239, 68);
    border-radius: 9999px;
    width: .5rem;
    height: .5rem;
    display: inline-flex;
    position: relative;
}

.status-text {
    padding-left: 1.25rem;
    color: #A1A1A1;
    font-weight: 300;
}

@keyframes ping {
    75%, 100% {
        transform: scale(2);
        opacity: 0;
      }
}

@media screen and (min-width: 1025px) {
    .project {
        flex-direction: row;
    }    
}
.experience {
    display: flex;
    flex-direction: column;
    color: white;
    font-size: 1.2rem;
    font-weight: 300;
  }
  
.bold {
    font-weight: 500;
}


.grey {
    color: #A1A1A1;
}

.forms {
    padding: 1.5rem;
    border: 0 solid rgb(68,68,68);
    border-width: 1px;
    border-radius: 1rem;
}

.title-form {
    display: flex;
    flex-direction: column;
}

.title-desc {
    color: #A1A1A1;
    font-size: 1rem;
    font-weight: 200;
}

.form {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}

.form-user {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;

}

.input-label {
    display: flex;
    flex-direction: column;
    color: white;
    width: 100%;
}

.input-label input, textarea {
    width: 100%;
    font-size: .875rem;
    font-weight: 200;
    line-height: 1.25rem;
    background-color: transparent;
    border: 0 solid rgb(68,68,68);
    border-width: 1px;
    border-radius: .2rem;
    padding-left: .75rem;
    padding-right: .75rem;
    padding-top: .25rem;
    padding-bottom: .25rem;
    color: white;
}
.input-label input,textarea::placeholder {
    color: #A1A1A1;
}

.input-label input:focus{
    border-color: white;
    outline: none; /* Remove o outline padrão */
}

.input-label textarea:focus {
    outline: none;
    border-color: white;
}

textarea {
    min-height: 100px;
}

.form-submit {
    font-size: 1rem;
    font-weight: 200;
    padding-top: .35rem;
    padding-bottom: .35rem;
    border: none;
    border-radius: .7rem;
    cursor: pointer;
}
